<template>
  <form @submit.prevent="AddEgreso" >
            <div>
                             
                 <input
                    type='date'
                    name='fecha'
                    class='form-control mt-3'
                    placeholder='Fecha'
                    autoComplete='off'
                    v-model="egreso.fecha"
                /> 
            
                
                <input
                    type='text'
                    name='descripcion'
                    class='form-control mt-3'
                    placeholder='Descripcion'
                    autoComplete='off'
                    v-model="egreso.descripcion"

                />
                 <input
                    type='number'
                    name='monto'
                    class='form-control mt-3'
                    placeholder='Monto'
                    autoComplete='off'
                    v-model="egreso.monto"

                />
                

                <button 
                class='btn btn-primary form-control  mt-2'
                type='submit'  
                :disabled="getIsLoading"
                >
                Guardar

                </button>

            </div>
        </form>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
    data() {
        return{
            egreso:{}
        }
    },
    computed:{
        ...mapGetters('egreso',['getIsLoading'])
    },
    methods:{
        ...mapActions('egreso',['addEgreso']),
        async AddEgreso(){
            var egresoAdd = {
                cedula: this.egreso.cedula,
                fecha: this.egreso.fecha,
                descripcion: this.egreso.descripcion,
                monto: this.egreso.monto
            }
            await this.addEgreso(egresoAdd)
        }
    },
}
</script>

